.Banner {
    height: 400px;
    position: relative;
}

.Banner .Media {
    background-color: white;
    height: 100%;
    overflow: hidden;
    position: relative;
    transition: 300ms;
    cursor: pointer;
}

.Banner .Media .MediaCaption {
    text-overflow: ellipsis;
    position: absolute;
    bottom: 0;
    padding: 15px;
    background-color: black;
    color: white;
    opacity: 0.6;
    width: 100%;
    height: 10%;
    font-size: 25px;
    font-weight: 200;
    transition: 300ms;
    cursor: pointer;
}

.Banner .Media .MediaCaption:hover {
    opacity: 0.8;
}

.Banner .Media:hover {
    filter: brightness(115%);
}

.Banner .BannerGrid {
    height: 100%;
}

.Banner .Content {
    color: #000;
    background-color: #e3e3e3;
    height: 100%;
    cursor: pointer;
    padding: 30px;
    transition: 300ms;
}

.Banner .Content:hover, .Banner .Content:active {
    background-color: #f1efef;
}

.Banner .Content:hover .ViewButton, .Banner .Content:active .ViewButton {
    background-color: #d6d6d6;
    color: #000;
}

.Banner .Content .Title {
    font-size: 40px;
    font-weight: 500;
}

.Banner .Content .Caption {
    margin-top: 10px;
    font-size: 21px;
}

.Banner .Content .ViewButton {
    margin-top: 40px;
    color: #000;
    font-size: 25px;
    border: 3px solid #000;
    text-transform: capitalize;
    transition: 200ms;
}
